<template>
  <div class="link">
    <div class="demo">
      <ul>
        <li
          :class="indexflag == index ? 'active' : ''"
          @click="dj(index)"
          v-for="(item, index) in list"
          :key="index"
        >
          <p v-show="indexflag == index">{{ msg }}</p>
        </li>
      </ul>
      <input type="text" v-model="msg" />
      <ul>
        <li v-for="(items, indexs) in lists" :key="indexs">{{ items.msg }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import linkage from "../components/linkage.vue";
import bus from "../bus";
export default {
  watch: {
    msg(val) {
      this.msg = val;
      console.log(this.msg);
      if (this.msg == "") {
        this.lists = this.listgl;
      } else {
        this.lists = this.lists.filter(
          (item) => item.msg.indexOf(this.msg) != -1
        );
        console.log(this.lists);
      }
    },
  },
  data() {
    return {
      ruleForm: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" },
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "请选择时间",
            trigger: "change",
          },
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change",
          },
        ],
        resource: [
          { required: true, message: "请选择活动资源", trigger: "change" },
        ],
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }],
      },
      msg: "",
      indexflag: null,
      listgl: [
        { msg: "aaa" },
        { msg: "aaab" },
        { msg: "ccc" },
        { msg: "awsda" },
        { msg: "wdasd" },
        { msg: "fasfsa" },
      ],
      lists: [
        { msg: "aaa" },
        { msg: "aaab" },
        { msg: "ccc" },
        { msg: "awsda" },
        { msg: "wdasd" },
        { msg: "fasfsa" },
      ],
      list: [{ title: "1" }, { title: "2" }, { title: "3" }, { title: "4" }],
      msg: "",
    };
  },
  methods: {
    dj(e) {
      this.indexflag = e;
      this.msg = this.list[e].title;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted() {},
};
</script>

<style scoped>
.link {
  width: 504px;
  height: 41px;
  position: absolute;
  top: 26px;
  left: 469px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 26px;
}
.demo {
  width: 500px;
  height: 500px;
  background: red;
  position: absolute;
  top: 200px;
}
.demo ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.demo ul .active {
  width: 70px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: green;
  color: aliceblue;
}
.demo ul li {
  width: 70px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: white;
}
</style>